                  <img :src="rlogo" alt="" width="30%" />
                </div>
            </div>
              </div>
            <v-file-input type="file" accept="image/png, image/jpeg, image/bmp" placeholder="Upload a Photo" prepend-icon="mdi-upload" label="Upload Photo" @change="this.onImageChange" :clearable="false" v-model="images" show-size></v-file-input>
            <center>
                or
            </center>
            <v-file-input class="hidden-md-and-up" type="file" accept="image/*" placeholder="Pick a Photo" prepend-icon="mdi-camera" label="Take Photo" capture="" @change="this.onImageChange" :clearable="false" v-model="images" show-size></v-file-input>
        </v-flex>
        <v-flex xs12 md6 pa-5>
            <div id="my-node-">
                <v-img :src="imageUrl" width="100%" height="auto" alt="Alternative" class="firstImage">
                    <v-img :src="rlogo" alt="" width="100%" class="secondImage" />
                </v-img>
            </div>
        </v-flex>
    </v-layout>
    <div>
        <div id="my-node">
