        //                 myCanvas.width = 800; // Assigns image's width to canvas
        //                 myCanvas.height = 800 // Assigns image's height to canvas
        //                 myContext.drawImage(myImage, 0, 0, 800, 800); // Draws the image on canvas
        //                 // let imgData = myCanvas.toDataURL("image/jpeg",0.75); // Assigns image base64 string in jpeg format to a variable
        //             }
        //             var cropper = canvas.cropper({
        //        aspectRatio: 16 / 9
        //      });
        //      console.log(cropper);
                     
        //         }
        //     }
        // });
    },
    watch: {
       exampleContent: function () {
            console.log("exampleContent changed to " + this.exampleContent);
        }
    },
    methods: {
       
        onSave() {
            // var button = document.getElementById('btn-download');
            var canvas = document.getElementById("myCanvas");
            console.log(canvas);
            //   var dataURL = canvas.toDataURL('image/png');

            //   var link = document.createElement('a');
            // link.download = 'filename.png';
            // link.href = dataURL
            // link.click();

            var FileSaver = require('file-saver');
            var node = document.getElementById('my-node');
            var today = new Date();
            var name = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate() + today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
            //        domtoimage.toPng(node)
            // .then(dataUrl => {
            //   const link = document.createElement('a');
            //   link.download = 'image.png';
            //   link.href = dataUrl;
            //   link.click();
            // })
            domtoimage.toBlob(node)
                .then(function (blob) {
                    FileSaver.saveAs(blob, name + 'Rotaract-Club-of-Majestic-CBE.png');
                }).catch(function (error) {
                    console.error('oops, something went wrong!', error);
                });
        },
        onImageChange() {
           this.imageUrl = URL.createObjectURL(this.images)
        }
